.tabs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  list-style: none;
  margin: 20px 0 0;
  padding: 0;
}

.list {
  overflow: overlay;
  scrollbar-gutter: stable;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 40px;
  margin: 0;
  padding: 40px 0 0;
  max-height: 756px;
}

.list::-webkit-scrollbar {
  width: 8px;
  overflow: visible;
}

.list::-webkit-scrollbar-thumb {
  background-color: #8585ad;
}

.list::-webkit-scrollbar-track {
  background: #2f2f37;
}
